.container {
  width: 54px;
  height: 30px;
  background: #a2a2a2;
  border: 1px solid #565656;
  display: inline-block;
  border-radius: 15px;
  margin: 0px;
  vertical-align: bottom;
  cursor: pointer;
  transition: all 0.2s;
}

.container.selected {
  background: #2c86ff;
  border: 1px solid #0f4b9c;
}

.knob {
  width: 26px;
  height: 26px;
  background: #fff;
  position: relative;
  top: 2px;
  left: 2px;
  border-radius: 13px;
  transition: all 0.2s;
}

.container.selected .knob {
  left: 26px;
}
