.navbar {
  height: 60px;
  background: #1a245f;
  line-height: 60px;
  text-align: center;
}

.navbar a {
  display: block;
  padding: 0 12px;
  color: black;
  text-decoration: none;
}

.drawer {
  position: fixed;
  height: 100vh;
  top: 0;
  right: -210px;
  width: 70%;
  max-width: 200px;
  background: #e6e6e6;
  -webkit-box-shadow: -5px 7px 8px -7px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -5px 7px 8px -7px rgba(0, 0, 0, 0.75);
  box-shadow: -5px 7px 8px -7px rgba(0, 0, 0, 0.75);
  z-index: 100;
  transition: all 0.2s;
  padding: 60px 20px;
  text-align: left;
  box-sizing: border-box;
  overflow: scroll;
}

.drawer.isOpen {
  right: 0;
}

.hamburgerIcon {
  position: absolute;
  right: 10px;
  top: 10px;
  z-index: 101;
}

.title {
  color: #fff;
}
