.container {
  padding: 0;
  margin: 0 auto;
}

@media only screen and (max-width: 500px) {
  .container {
    width: 400px;
  }
  .diagram {
    transform: scale(0.8);
  }
}

@media only screen and (max-width: 400px) {
  .container {
    width: 250px;
  }
  .diagram {
    transform: scale(0.5);
  }
}

.diagram {
  width: 500px;
  margin: 0 auto;
  position: relative;
  transform-origin: top left;
}

.img {
  width: 100%;
  opacity: 0.5;
}

.people {
  position: absolute;
}

.device {
  position: absolute;
  font-size: 60px;
  cursor: pointer;
}

.device img {
  width: 50px;
}

.device .tooltip {
  font-size: 12px;
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
}

.device:hover .tooltip {
  visibility: visible;
}

.genericButton,
.motionButton {
  width: 182px;
  padding: 8px;
  border-radius: 999px;
  border: 5px solid #848484;
  margin: 4px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  vertical-align: top;
  box-sizing: border-box;
  user-select: none;
}
.motionButton img {
  width: 40px;
  height: 40px;
}
.motionButton span {
  display: inline-block;
  height: 40px;
  vertical-align: top;
  line-height: 14px;
}

.genericButton {
  cursor: pointer;
}
.genericButton [role="img"] {
  width: 40px;
  height: 40px;

  font-size: 32px;
  margin-right: 6px;
}

.genericButton .content {
  vertical-align: top;
  line-height: 40px;
}
