.imageContainer {
  text-align: center;
}

.image {
  margin: 10px;
  height: 150px;
  width: auto;
  display: inline-block;
  box-sizing: border-box;
  top: 0;
  border: 1px solid #000;
  vertical-align: top;
}

.viewDevicesContainer {
  text-align: center;
}

.viewDevicesContainer input {
  padding: 20px;
  border: 1px solid #c7c7c7;
  border-radius: 3px;
  background: #e9f0f7;
  color: #000;
  font-size: 20px;
  margin-top: 20px;
}

.comingHomeContainer {
  text-align: center;
}

.comingHomeContainer input {
  width: 50%;
  max-width: 300px;
  padding: 20px;
  height: 100vh;
  max-height: 75px;
  border: 1px solid #c7c7c7;
  border-radius: 3px;
  background: #e9f0f7;
  font-size: 20px;
  margin-top: 20px;
}

.comingHomeContainer .response {
  border: none;
  margin: 20px auto;
  display: block;
}

.comingHomeContainer .responseError {
  border: none;
  margin: 20px auto;
  display: block;
  color: #f00;
}

.lightsSection {
  margin-top: 64px;
}

.notificationBanner {
  background: #ffb52c;
  padding: 20px;
  color: black;
  text-align: center;
}
