.circle {
  width: 12px;
  height: 12px;
  position: absolute;
  top: 10px;
  left: 10px;
  border-radius: 6px;
}

.black {
  background: #000000;
}
.red {
  background: #ff0000;
}
.reallyGreen {
  background: #00be19;
  border: 1px solid #ffffff;
}
.green {
  background: #00be19;
}
.blue {
  background: #282bfc;
}
