.container {
  text-align: center;
  margin-top: 30px;
}

.inputField {
  padding: 10px;
  border: 1px solid #ccc;
  background: #fff;
  margin: 4px;
}

.footageBar {
  width: 100%;
  height: 20px;
  background: #ccc;
  margin: 20px 0;
}

.footage {
  position: absolute;
  height: 14px;
  margin: 3px 0;
  /* background: #f00; */
  box-sizing: border-box;
  /* border: 1px solid black; */
  cursor: pointer;
}

.controlButton {
  font-size: 30px;
  display: inline-block;
  margin: 20px;
  cursor: pointer;
  text-decoration: none;
}

.previewVideo {
  display: inline-block;
  border: 1px solid #000;
  margin: 10px;
  width: 320px;
  height: 180px;
}
