.overlay {
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  top: 0;
  left: 0;
}
.fade {
  opacity: 0.5;
  background: #000;
}
.modal {
  width: 400px;
  margin: auto;
  background: #222;
  color: #fff;
  border: 1px solid #6b6b6b;
  border-radius: 3px;
  padding: 16px;
  box-sizing: border-box;
}

.bottomLockContainer {
  position: fixed;
  bottom: 0;
  width: 100%;

  background: #222;
  color: #fff;
  border: 1px solid #6b6b6b;
}

.bottomLock {
  margin: 0 auto;
  padding: 20px;
  width: 400px;
  box-sizing: border-box;
}

.section {
  color: #fff;
  margin-top: 20px;
}

.title {
  display: flex;
  justify-content: space-between;
}
.title span {
  margin: auto 0;
  font-size: 22px;
}
