.container {
  position: relative;
  display: inline-block;
  width: 182px;
  margin: 4px;
  border-radius: 999px;
  border: 5px solid #848484;
  vertical-align: top;
  box-sizing: border-box;

  user-select: none;
  touch-action: none;
}

.fill,
.fillOn {
  pointer-events: none;
  flex: 1;
  padding: 8px;
  border-radius: 999px;
}

.fillOn {
  background-color: yellow;
}

/* .fill:hover {
  background-size: 300px 150px;
} */
.content {
  position: relative;
  pointer-events: none;
  white-space: nowrap;
  color: #000;
  overflow: hidden;
  text-align: center;
}

.automation {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 4px;
  top: 8px;
  pointer-events: all;
  font-size: 36px;
  cursor: pointer;
}

.content span.icon {
  cursor: pointer;
  pointer-events: all;
  width: 30px;
  height: 40px;
  font-size: 30px;
  line-height: 40px;
}

.content span {
  display: inline-block;
  height: 40px;
  vertical-align: top;
  line-height: 20px;
}
