.container {
  position: relative;
  display: inline-block;
  width: 182px;
  margin: 4px;
  border-radius: 999px;
  border: 5px solid #848484;
  vertical-align: top;
  box-sizing: border-box;

  user-select: none;
  touch-action: none;
}
.fill {
  pointer-events: none;
  flex: 1;
  padding: 8px;
  border-radius: 999px;
  background: transparent;
  background-image: url(/home/blue_wave.png);
  background-repeat: repeat-x;

  background-size: 200px 150px;
  transition: 1.5s background-size;

  filter: hue-rotate(180deg) brightness(2.5);
}
/* .fill:hover {
  background-size: 300px 150px;
} */
.content {
  position: relative;
  pointer-events: none;
  white-space: nowrap;
  color: #000;
  overflow: hidden;
}

.automation {
  width: 40px;
  height: 40px;
  position: absolute;
  right: 4px;
  top: 8px;
  pointer-events: all;
  font-size: 36px;
  cursor: pointer;
}

.bulb {
  cursor: pointer;
  pointer-events: all;
  width: 40px;
  height: 40px;
}

.content span {
  display: inline-block;
  height: 40px;
  vertical-align: top;
  line-height: 20px;
}
